.home {
    background-color: rgb(216, 233, 238);
    padding-bottom: 5%;
}
.cardC {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    width: 70%;
    margin: 0 auto;
  }
.optC {
    width: 64%;
    margin: 5px auto;
}
.optm{
    color: #0084ff;
    font-size: 11px;
    border: 1px solid #0084ff;
    border-radius: 20px;
    padding: 5px 14px;
    display: inline-block;
    cursor: pointer;
}
.optC1{
    background-color: lightblue;
}
.filterText{
    font-size: 14px;
    font-weight: 600;
    margin-left: 13px;
    line-height: 2;
  }
  .filterIcon{
    color: #0084ff;
    margin-left: 5px;
  }
  @media (max-width: 930px) {
    .cardC{
      width: 80%;
    } 
    .optC{
      width: 80%;
    }
    @media (max-width: 500px) {
      .cardC {
        width: 70%;
      } 
      .optC{
        width: 100%;
        padding:0 3% ;
      }
  }
  }