@import url('https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,800;0,900;1,700;1,800;1,900&display=swap');
.nav {
  display: flex;
  background-color: white;
  justify-content: space-between;
  align-items: center;
  padding: 16px 78px;
  background-color: #fff;
  height: 60px;
  border-bottom: 1px solid grey;
}
.edyoda {
  margin-top: 5px;
  font-family: Raleway, sans-serif;
  font-size: 32px;
  font-weight: 900;
  text-align: left;
  color: #0084ff;
}
.stories {
  margin: 0;
  font-family: Raleway, sans-serif;
  font-size: 18px;
  font-weight: 500;
  font-style: italic;
  color: #000;
  margin-top: -1%;
}
.dropdownText {
  margin: 0;
  font-weight: 300;
  text-align: center;
  font-size: 18px;
  font-family: Raleway, sans-serif;
}
.nav button {
  width: auto;
  font-size: 0.75em;
  padding: 0.5em 2em;
  margin: 0px ;
  box-shadow: none;
  min-width: 136px;
  height: 42px;
  border-radius: 25px;
  border: none;
  cursor: pointer;
  color: #fff;
  outline: none;
  font-weight: 600;
  font-style: normal;
  font-stretch: normal;
  letter-spacing: 0.03em;
  background: linear-gradient(269deg, #0048ff, #0096ff);
  
}
.nav div:nth-of-type(2) {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 45%;
  
}
.nav div:nth-of-type(2)>p{
    min-width: 23em;
    opacity: .6;
    font-family: Raleway,sans-serif;
    font-size: 16px;
    font-weight: 500;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.2;
    letter-spacing: .4px;
    text-align: left;
    color: rgba(0,0,0,.6);
    margin-top: .9em;
    margin-right: -3%;
}
@media (max-width: 1100px) {
  .nav div:nth-of-type(2)>p {
    font-size: 12px;
  }
}
@media (max-width: 930px) {
  .nav div:nth-of-type(2)>p {
    display: none;
  }
  .nav button {
   width: auto;
   padding: unset;
  }
  .nav div:nth-of-type(2){
    justify-content: flex-end;
    align-items: center;
  } 
  .nav{
    padding: 15px 30px
  };
  @media (max-width: 930px) { 
  }
  @media (max-width: 660px) {
    .dropdownText  {
    display: none;
    }
}
@media (max-width: 660px)
 {
.nav{
  padding: 15px 15px;
}

}

}
