@import url('https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100;0,200;0,300;1,100;1,200&display=swap');
.card {
  width:  28%;
  margin: 5px 16px;
  border: 1px solid lightblue;
  max-height: 400px;
  background-color: white;
  overflow: hidden;
  border-radius: 10px;
  /* background-color: tomato; */
  padding-bottom: 1%;
  margin-bottom: 19px;
}
img {
  width: 100%;
  height: 150px;
}
.descdiv {
  padding: 0px 4%;
  padding-bottom: 3%;
}
.descdiv > div:nth-of-type(1) {
  font-family: Raleway;
  text-align: left;
  font-size: 12px;
  font-weight: 600;
  line-height: 1.2;
  color: #000;
  box-sizing: border-box;
  height: 60px;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;  
  overflow: hidden;
  letter-spacing: 0.5px;
}
h2{
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-weight: 700;
  opacity: 0.7;
}
.authop{
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-top: 0; 
  white-space: nowrap;
  overflow: hidden;
  text-overflow: clip;
  text-align: left;
}
.author{
  padding-top: 4px;
  color: #0096ff;
  font-size: 14px;
  font-weight: 500;
  letter-spacing: 0.5px;
  margin-right: 2%;
}
.date{
    color: rgba(0, 0, 0, 0.7);
    font-size: 14px;
    font-weight: 300;
    padding-right: 2px;
    letter-spacing: 1px;
    display: inline-block;
    width: 32.6%;
    overflow: hidden;
}
.descp{
  display: -webkit-box;
  -webkit-line-clamp: 5;
  -webkit-box-orient: vertical;  
  overflow: hidden;
  opacity: .5;
  font-family: Raleway;
  font-size: 14px;
  font-weight: 400;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.43;
  letter-spacing: .35px;
  text-align: left;
  color: #000;
  margin: .3em 0;
}



@media (max-width: 930px) {
  .card {
    width: calc(45% - 16px);
  }
}

@media (max-width: 660px) {
  .card {
    width: calc(48% - 8px);
    margin: 5px 4px;
  }

  @media (max-width: 500px) {
    .card {
      width: calc(100%);
    }
}
}